	import calendar from "@/components/uni-calendar/uni-calendar"

    export default {
		components:{
			calendar
		},
        data() {
            return {
                videoCtx: null,
                disable: false,
				 date:'2019-03-10'
            }
        },
        mounted() {
            this.videoCtx = uni.createVideoContext('demoVideo')
        },
        methods: {
            play(event) {
                this.videoCtx.play();
                uni.showToast({
                    title: '开始播放',
                    icon: 'none'
                });
            },
            pause(event) {
                this.videoCtx.pause();
                uni.showToast({
                    title: '暂停播放',
                    icon: 'none'
                });
            },
			change(e) {
				console.log(e);
			},
			toClick(e) {
				console.log(e);
			}
        }
    }
